a {
  text-decoration: none;
  color: inherit;
}

.main {
  display: flex;
  align-items: center;
}
.logo-img {
  width: 120px;
}
.search {
  margin-left: 20px;
}

.login {
  display: flex;
  justify-content: flex-end;
  flex-grow: 1;
}
.login-btn {
  display: flex;
  align-items: center;
  margin-right: 20px;
}
.signup-btn {
  border: 1px #5292f3d0 solid;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}
.signup-btn:active {
  background-color: #5292f3d0;
  color: white;
}

.menu {
  display: inline-flex;
}
.menu div {
  padding: 5px 10px 10px;
  margin-top: 10px;
  margin-right: 20px;
  cursor: pointer;
}
.menu div:hover {
  box-shadow: inset 0 -3px 0 0 #5292f3d0;
}
